<template>
  <div id="app">
    <h1>Liveness Check</h1>
    <video ref="video" autoplay></video>
    <canvas ref="canvas"></canvas>
    <p v-if="livenessDetected">Liveness detected!</p>
    <p v-else>Liveness check in progress...</p>
  </div>
</template>

<script>
import * as faceapi from 'face-api.js';

export default {
  data() {
    return {
      livenessDetected: false,
      previousFacePosition: null,
      blinkCount: 0,
    };
  },
  async mounted() {
    try {
      await this.initLivenessCheck();
    } catch (error) {
      console.error('Error initializing liveness check:', error);
    }
  },
  methods: {
    async initLivenessCheck() {
      try {
        // Load face-api.js models
        await faceapi.nets.tinyFaceDetector.loadFromUri('/models');
        await faceapi.nets.faceLandmark68Net.loadFromUri('/models');
        console.log('Models loaded successfully.');

        // Start video stream
        await this.startVideo();
      } catch (error) {
        console.error('Error loading models or starting video:', error);
      }
    },
    async startVideo() {
      try {
        const video = this.$refs.video;
        const stream = await navigator.mediaDevices.getUserMedia({ video: {} });
        video.srcObject = stream;

        // Handle video metadata loaded
        video.onloadedmetadata = () => {
          this.detectFace();
        };
      } catch (error) {
        console.error('Error accessing webcam:', error);
      }
    },
    async detectFace() {
      const video = this.$refs.video;
      const canvas = this.$refs.canvas;

      // Set canvas size to match the video
      canvas.width = video.videoWidth;
      canvas.height = video.videoHeight;

      const displaySize = { width: video.videoWidth, height: video.videoHeight };
      faceapi.matchDimensions(canvas, displaySize);

      setInterval(async () => {
        try {
          // Detect face and landmarks
          const detections = await faceapi.detectAllFaces(video, new faceapi.TinyFaceDetectorOptions()).withFaceLandmarks();

          if (detections.length > 0) {
            const resizedDetections = faceapi.resizeResults(detections, displaySize);
            faceapi.draw.drawDetections(canvas, resizedDetections);
            faceapi.draw.drawFaceLandmarks(canvas, resizedDetections);

            const facePosition = resizedDetections[0].detection.box;

            // Check for head movement
            if (this.previousFacePosition && this.hasFaceMoved(this.previousFacePosition, facePosition)) {
              this.livenessDetected = true;
            }

            // Check for blink (using simple heuristic)
            const landmarks = resizedDetections[0].landmarks;
            const leftEye = landmarks.getLeftEye();
            const rightEye = landmarks.getRightEye();

            if (this.isEyeClosed(leftEye) || this.isEyeClosed(rightEye)) {
              this.blinkCount++;
              if (this.blinkCount > 2) {
                this.livenessDetected = true;
              }
            } else {
              this.blinkCount = 0;
            }

            // Update previous face position
            this.previousFacePosition = facePosition;
          }
        } catch (error) {
          console.error('Error during face detection:', error);
        }
      }, 100);
    },
    hasFaceMoved(prevPosition, currentPosition) {
      const threshold = 10; // Minimum movement to consider head movement
      return Math.abs(currentPosition.x - prevPosition.x) > threshold ||
             Math.abs(currentPosition.y - prevPosition.y) > threshold;
    },
    isEyeClosed(eye) {
      const eyeHeight = Math.abs(eye[1].y - eye[5].y); // Compare top and bottom points
      return eyeHeight < 5; // Simple threshold to determine if the eye is closed
    }
  }
};
</script>

<style scoped>
#app {
  text-align: center;
}

video {
  display: block;
  margin: 0 auto;
}

canvas {
  position: absolute;
  top: 0;
  left: 0;
}
</style>
